<template>
  <Layout :UserNotif="notif.length" :CaseNotif="notifCase.length">
    <PageHeader :title="title" :items="items" />
    <div class="row mb-4">
      <div class="col-12">
        <div class="card">
          <div class="card-body">
            <div class="row mt-5 mb-5 pb-4 mx-auto" v-if="loading">
                <div class="col-12 p-5 m-4 text-center">
                    <div class="spinner-border text-secondary" role="status" v-if="loading">
                        <span class="sr-only">Loading..</span>
                      </div>
                </div>
            </div>
            <div v-if="user_type == 'su' || user_type == 'adm'">
             <div class="row mb-2" v-if="!loading">
                 <div class="col-5 col-xl-7">
                  <div class="">
                      <b-button class="btn-group mr-2 mb-2 mb-sm-0 border rounded" @click="allNotif('customer')" :variant="this.notifCustomer ? 'light' : 'white' ">
                        {{ $t('notification.new-customer') }}  <span class="badge badge-soft-primary ml-1 rounded mt-1">{{notif.length}}</span>
                      </b-button>
                      <b-button class="btn-group mr-2 mb-2 mb-sm-0 border rounded" @click="allNotif('cases')" :variant="this.notifCases ? 'light' : 'white' ">
                        {{ $t('notification.new-case') }} <span class="badge badge-soft-primary ml-1 rounded mt-1">{{notifCase.length}}</span>
                      </b-button>
                      
                      <!-- <b-dropdown class="btn-group mr-2 mb-2 mb-sm-0 border" variant="white" >
                        <template slot="button-content">
                          {{ $t('button.bulk-action') }}
                          <i class="mdi mdi-chevron-down ml-2"></i>
                        </template>
                        <button class="dropdown-item" @click="bulkAction()">{{ $t('button.hapus.text') }}</button>
                      </b-dropdown> -->
                  </div>
              </div>
              <div class="col-4 col-xl-3">
              </div>
                <div class="col-3 col-xl-2 text-right">
                  <!-- <div class="form-group has-search">
                    <span class="bx bx-search-alt search-icon form-control-feedback"></span>
                      <b-form-input
                      v-model="filter"
                      type="search"
                      :placeholder="$t('navbar.search.text')"
                      class="form-control"
                    ></b-form-input>
                  </div> -->
                    <b-button
                        @click="readAll"
                        variant="success"
                        class="btn mr-1"
                      >
                      <b-spinner variant="light" small v-if="loading_all" class="mr-1"></b-spinner>  {{ $t("notification.mark-all-as-read") }}
                      </b-button>
              </div>
              <!-- end col-->
             </div>
             </div>

            <!-- Table -->
            <div class="mb-0" :class="{ 'pb-5' : rows < 10 }">
              <div v-if="!loading">
                <div v-if="notifCustomer">
                <h5 class="mt-5"><b>{{ $t('notification.new-customer') }}</b></h5>
                  <div v-for="ntf in notif" :key="ntf.id" class="border mb-2 p-2">
                  <div class="row p-1">
                    <div class="col-10">
                          <div class="media">
                            <div class="mr-2">
                              <i class="bx bx-user-plus h2 text-primary"></i>
                            </div>
                            <div class="media-body">
                              <div>
                                <h6 class="text-muted">{{ $t('notification.new-customer') }}, <b><router-link :to="'/customer-management-detail/'+ntf.data.id_user" class="text-muted">{{ntf.data.name}} </router-link></b></h6>
                                <span class="text-muted">
                                    {{ntf.data.email}}
                                </span> <br>
                                <span class="text-muted ">
                                <p class="mt-2 mb-0">
                                    {{ntf.created_at}}
                                </p>
                                </span>
                              </div>
                            </div>
                          </div>
                    </div>
                    <div class="col-2 text-right">
                          <input type="radio" id="my-radio" v-on:click="readCustomer(ntf.id)" :checked="ntf.read_at" class="mr-2 mt-3 text-primary" >
                    </div>
                  </div>
                
                  </div>
                     <div v-if="!notif[0] && !loading">
                      <div class="row mt-5 mb-5 pb-4 mx-auto">
                          <div class="col-12  p-5 my-5 text-center">
                              <span class="text-secondary">{{ $t('all.dnf') }}</span>  
                          </div>
                      </div>
                      
                  </div>
                </div>
                <div v-if="notifCases">
                  <h5 class="mt-5"><b>{{ $t('notification.new-case') }}</b></h5>
                  <div v-for="ntf in notifCase" :key="ntf.id" class="border mb-2 p-2">
                    <div class="row p-1">
                    <div class="col-10">
                          <div class="media">
                            <div class="mr-2">
                                <i class="bx bx-copy-alt h2 text-primary"></i>
                            </div>
                            <div class="media-body">
                              <div>
                                <h6 class="text-muted">{{ $t('notification.new-case') }}, <b>{{ntf.data.category}} </b></h6>
                                <span class="text-muted">
                                  <router-link :to="'/case-management-detail/'+ntf.data.id_case" class="text-muted"> {{ntf.data.id_case}}, {{ntf.data.classification}}</router-link>
                                </span> <br>
                                <span class="text-muted ">
                                <p class="mt-2 mb-0">
                                    {{ntf.created_at}}
                                </p>
                                </span>
                              </div>
                            </div>
                          </div>
                    </div>
                    <div class="col-2 text-right">
                          <input type="radio" id="my-radio" v-on:click="readCase(ntf.id)" :checked="ntf.read_at" class="mr-2 mt-3 text-primary" >
                    </div>
                  </div>
                 
                  </div>
                  <div v-if="!notifCase[0] && !loading">
                      <div class="row mt-5 mb-5 pb-4 mx-auto">
                          <div class="col-12  p-5 my-5 text-center">
                              <span class="text-secondary">{{ $t('all.dnf') }}</span>  
                          </div>
                      </div>
                  </div>
                </div>
                <div class="text-center mt-4" v-if="notif.length > 25">
                  <b-button class="btn-group mr-2 mb-2 mb-sm-0 border rounded" size="sm" variant="white">
                          {{ $t('notification.load-more') }}
                      </b-button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </Layout>
</template>

<script>
import Vue from "vue";
import Swal from "sweetalert2";
import Layout from "../../layouts/main";
import PageHeader from "@/components/page-header";
import appConfig from "@/app.config";
import { required, sameAs } from "vuelidate/lib/validators";
import UserService from "@/helpers/user.service";
import NotificationService from "@/helpers/notification.service";
import store from '@/state/store';
import moment from "moment";
// import NavBar from "@/components/nav-bar.vue";

export default {
  page: {
    meta: [{ name: "description", content: appConfig.description }],
  },
  components: { Layout, PageHeader },
  data() {
    return {
      answer: 21,
      urlImage: process.env.VUE_APP_BASE_API_URL_AVATAR,
      user_type: store.state.authBackend.user.type,
      title: this.$t("notification.notification-management"),
      items: [
        {
          text: this.$t("menuitems.dashboards.text"),
          href: "#",
        },
        {
          text: this.$t("notification.notification-management"),
          active: true,
        },
      ],
      loading_all:false,
      notifCustomer:true,
      notifCases:false,
      loading:false,
      tableData:[],
      notifCase:[],
      notif:[
      //   {
      //     id: 1,
      //     type: 'App\Notifications\NewCustomerNotification',
      //     notifiale_type: 'App\Models\User',
      //     notifiable_id: 5,
      //     data:'{"id":"5abb6c08-a1ac-4a02-ad55-b39e1454a565","name":"aulia","email":"aulaaAsd@gmail.com"}',
      //     read_at: 1,
      //     created_at: '2022-04-25, 09:40:28',
      // },{
      //     id: 2,
      //     type: 'App\Notifications\NewCustomerNotification',
      //     notifiale_type: 'App\Models\User',
      //     notifiable_id: 5,
      //     data:'{"id":"5abb6c08-a1ac-4a02-ad55-b39e1454a567","name":"yusuf","email":"yusuf@gmail.com"}',
      //     read_at: 1,
      //     created_at: '2022-04-25, 09:40:28',
      // }, {
      //     id: 3,
      //     type: 'App\Notifications\NewCustomerNotification',
      //     notifiale_type: 'App\Models\User',
      //     notifiable_id: 5,
      //     data:'{"id":"5abb6c08-a1ac-4a02-ad55-b39e1454a567","name":"paijo","email":"paijo@gmail.com"}',
      //     read_at: 1,
      //     created_at: '2022-04-25, 09:40:28',
      // }, {
      //     id: 4,
      //     type: 'App\Notifications\NewCustomerNotification',
      //     notifiale_type: 'App\Models\User',
      //     notifiable_id: 5,
      //     data:'{"id":"5abb6c08-a1ac-4a02-ad55-b39e1454a567","name":"painem","email":"painem@gmail.com"}',
      //     read_at: null,
      //     created_at: '2022-04-25, 09:40:28',
      // }
      ],
      notif_total:'',
      all: true,
      totalRows: 1,
      currentPage: 1,
      perPage: 10,
      pageOptions: [10, 25, 50, 100],
      filter: null,
      filterOn: [],
      sortDesc: false,
      selected:[],
      selectAll: false,
      fields: [
        { key: "id", thClass: "d-none", tdClass: "d-none" },
        { key: "bulk",  thStyle: { width: "2%" }, label:'#'},
      ],
      submitted: false,
    };
  },
  validations: {
    form: {
        region_name: { required },
        region_number: { required }
    },
  },
  computed: {
    rows() {
      return this.tableData.length;
    },
  },
  methods: {
    list() {
      // this.loading = true;
      UserService.getProfile()
      .then(response => {
        // console.log( response.data.data)
        this.user_type = response.data.data.admin.role
      })
      .catch(e => {
        this.message = e.response.data.data
      });
    },

    getNotification() {
      this.loading = true;
      NotificationService.getNewNotification()
        .then((response) => {
          this.notif = [];
          // console.log(response.data.data);
          this.notif_total = response.data.data.length;
          response.data.data.forEach((val) => {
            const data = {
              id: val.id,
              type: val.type,
              notifiale_type: val.notifiale_type,
              notifiable_id: val.notifiable_id,
              data: val.data,
              read_at: val.read_at,
              created_at: this.formatDate(val.created_at),
              updated_at: this.formatDate(val.updated_at),
            };
            this.notif.push(data);
          });
          this.loading = false;
        })
        .catch((err) => {
          this.loading = false;
          console.log(err);
        });
    },

    getNotificationUpdate() {
      NotificationService.getNewNotification()
        .then((response) => {
          this.notif = [];
          // console.log(response.data.data);
          response.data.data.forEach((val) => {
            const data = {
              id: val.id,
              type: val.type,
              notifiale_type: val.notifiale_type,
              notifiable_id: val.notifiable_id,
              data: val.data,
              read_at: val.read_at,
              created_at: this.formatDate(val.created_at),
              updated_at: this.formatDate(val.updated_at),
            };
            this.notif.push(data);
          });
          this.loading = false;
        })
        .catch((err) => {
          this.loading = false;
          console.log(err);
        });
    },

    getNotificationCase() {
      // this.loading = true;
      NotificationService.getNewNotificationCase()
        .then((response) => {
          this.notifCase = [];
          // console.log(response.data.data);
          response.data.data.forEach((val) => {
            const data = {
              id: val.id,
              type: val.type,
              notifiale_type: val.notifiale_type,
              notifiable_id: val.notifiable_id,
              data: val.data,
              read_at: val.read_at,
              created_at: this.formatDate(val.created_at),
              updated_at: this.formatDate(val.updated_at),
            };
            this.notifCase.push(data);
          });
        })
        .catch((err) => {
          this.loading = false;
          console.log(err);
        });
    },

    getNotificationUnread() {
      // this.loading = true;
      NotificationService.getNewNotification()
        .then((response) => {
          this.notif = [];
          response.data.data.forEach((val) => {
            const data = {
              id: val.id,
              type: val.type,
              notifiale_type: val.notifiale_type,
              notifiable_id: val.notifiable_id,
              data: val.data,
              read_at: val.read_at,
              created_at: this.formatDate(val.created_at),
              updated_at: this.formatDate(val.updated_at),
            };
            if(val.read_at == 1){
              this.notif.push(data);
            }
          });
        })
        .catch((err) => {
          this.loading = false;
          console.log(err);
        });
    },

    allNotif(value){
      // console.log(value);
        if(value == 'customer'){
          this.notifCustomer = true;
          this.notifCases = false;
          // this.getNotificationUnread();;
        } else{
          this.notifCases = true;
          this.notifCustomer = false;
          // this.getNotification();
        }
    },

    readCustomer(id){
      let data = {
         "notif" : "customer",
         "id_notif" : id
      }
      // console.log(data);
        NotificationService.readCustomers(data)
        .then((response) => {
         this.getNotificationUpdate();
        })
        .catch((err) => {
          console.log(err);
        });
    },

    readCase(id){
      let data = {
         "notif" : "case",
         "id_notif" : id
      }
      console.log(data);
        NotificationService.readCases(data)
        .then((response) => {
         this.getNotificationCase();
        })
        .catch((err) => {
          console.log(err);
        });
    },

    readAll(){
      this.loading_all = true;
      let data = {
        "notif" : "customer"
      }
      let datacase = {
        "notif" : "case"
      }
      if(this.notifCustomer){
          NotificationService.readALL(data)
            .then((response) => {
              this.getNotification();
              this.loading_all = false;
            })
            .catch((err) => {
              console.log(err);
              this.loading_all = false;
            });
      } else {
          NotificationService.readALL(datacase)
            .then((response) => {
              this.getNotificationCase();
              this.loading_all = false;
            })
            .catch((err) => {
              console.log(err);
              this.loading_all = false;
            });
      }
    },

    unread(id){
      console.log('Unread', id);
        // NotificationService.unread(id)
        // .then((response) => {
        //  this.getNotification();
        // })
        // .catch((err) => {
        //   console.log(err);
        // });
    },

    // updateRead(id, read){
    //   console.log('update', id, read);
    //   if(read){
    //     this.read(id);
    //   } else {
    //     this.unread(id);
    //   }
    // },

    formatDate(value) {
      return moment(value).format("YYYY-MM-DD, hh:mm");
    },
  },
  beforeMount(){
    this.list();
  },
  mounted() {
    this.getNotification();
    this.getNotificationCase();

     window.Echo.channel('notif').listen('NotifCreated', (event) => {
        console.log('Berhasil Listen', event);
        this.getNotificationUpdate();
        this.getNotificationCase();
      })
  },
};
</script>

<style scoped>
.badge {
  border-radius: 2%;
}
#my-radio {
	accent-color: #174F80;
}

</style>
